* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}
nav {
  width: 100%;
  padding: 20px 5px;
  position: fixed;
  top: 0;
  left: 0;
  font-size: 1.5em;
  color: white;
  display: flex;
  cursor: pointer;
  flex-wrap: wrap;
  z-index: 9;
  background-color: black;
}
#logo {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.3em;
  letter-spacing: 0.25em;
  margin-left: 2.75em;
  text-transform: uppercase;
  mask-image: linear-gradient(
    -75deg,
    rgba(0, 0, 0, 0.6) 30%,
    #000 50%,
    rgba(0, 0, 0, 0.6) 70%
  );
  -webkit-mask-image: linear-gradient(
    -75deg,
    rgba(0, 0, 0, 0.6) 30%,
    #000 50%,
    rgba(0, 0, 0, 0.6) 70%
  );
  mask-size: 200%;
  -webkit-mask-size: 200%;
  -webkit-animation: shine 3s infinite;
          animation: shine 3s infinite;
}
@-webkit-keyframes shine {
  from {
    -webkit-mask-position: 150%;
  }

  to {
    -webkit-mask-position: -50%;
  }
}
@keyframes shine {
  from {
    -webkit-mask-position: 150%;
  }

  to {
    -webkit-mask-position: -50%;
  }
}
#logo img {
  width: 1.25em;
  height: 1.25em;
  margin-left: 0.25em;
  margin-right: 0.5em;
}
#navList {
  display: flex;
  margin: auto;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
}
#navList div {
  margin: 0 2em;
  transition: all 0.55s;
}
#navList div:hover {
  color: #39ff14;
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}
#res-contact {
  display: none;
}
#contactBtn {
  margin-right: 3em;
  padding: 7px 18px;
  border: 1px groove rgba(255, 255, 255);
  border-radius: 30px;
  transition: all 0.55s;
}
#contactBtn:hover {
  background-color: #39ff14;
  color: #000;
  border-color: #39ff14;
}
#collapseBtn {
  position: absolute;
  top: 21px;
  right: 20px;
  font-size: 1.5em;
  display: none;
}
.fullHeight{
  height: auto !important;
}
@media only screen and (max-width: 1150px) {
  #navList div{
    margin: 0 1em;
  }
}
@media only screen and (max-width: 992px) {
  #navList {
    height: 0;
    overflow-y: hidden;
    width: 100%;
    display: block;
    transition: 0.5s;
  }
  #contactBtn {
    display: none;
  }
  #res-contact {
    display: block;
  }
  #navList div {
    margin: 10vh auto;
    font-size: 1.25em;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    transition: 0.3s;
  }
  #navList div:first-child{
    margin-top: 8vh;
  }
  #logo {
    margin: auto;
    width: 100%;
    font-size: 1.75em;
    margin-left: 1.25em;
  }
  #collapseBtn {
    display: block;
    top: 27px;
  }
}
@media only screen and (max-width: 768px) {
  #logo {
    font-size: 1.8em;
  }
  #collapseBtn{
    font-size: 1.5em;
    top: 29px;
  }
  #navList div{
    font-size: 1.3em;
  }
}
@media only screen and (max-width: 620px) {
  #logo {
    margin-left: 0;
  }
}
@media only screen and (max-width: 550px) {
  #logo {
    font-size: 1.65em;
  }
  #collapseBtn{
    font-size: 1.3em;
    top: 27px;
  }
  #navList div{
    font-size: 1.2em;
  }
}
@media only screen and (max-width: 500px) {
  #logo {
    font-size: 1.3em;
    margin-left: -0.5em;
  }
  #collapseBtn{
    font-size: 1.2em;
    top: 25px;
  }
  #navList div{
    font-size: 1em;
  }
}
@media only screen and (max-width: 450px) {
  #logo {
    font-size: 1.2em;
  }
  #collapseBtn{
    font-size: 1.1em;
  }
}
@media only screen and (max-width: 380px) {
  #logo {
    font-size: 1em;
  }
  #collapseBtn{
    font-size: 0.8em;
  }
  #navList div{
    font-size: 0.8em;
  }
}
