  padding: 10px 10px;
  margin: 40px 0px;
}

/* OVERRIDING THE CSS OF EXTERNAL LIBRARY */

.slider-left div,
.slider-right div {
  border: none !important;
}

.react-3d-carousel .slider-container .slider-left div,
.react-3d-carousel .slider-container .slider-right div {
  font-size: 45px;
}

.react-3d-carousel .slider-container .slider-left div:hover,
.react-3d-carousel .slider-container .slider-right div:hover {
  color: var(--theme-color);
}

.react-3d-carousel .slider-container .slider-left {
  right: 120% !important;
}

.react-3d-carousel .slider-container .slider-right {
  left: 120% !important;
}

/* RESPONSIVE */

@media only screen and (max-width: 500px) {
  .headingLabel {
    font-size: var(--font-size-l);
  }
  .react-3d-carousel .slider-container .slider-left {
    right: 101% !important;
  }
  .react-3d-carousel .slider-container .slider-right {
    left: 101% !important;
  }
  .react-3d-carousel .slider-container .slider-left div,
  .react-3d-carousel .slider-container .slider-right div {
    font-size: 25px;
  }
}

