  display: none;
}

.front,
.back {
  position: absolute;
  width: 120px;
  height: 120px;
  /* -webkit-backface-visibility: hidden; 
    backface-visibility: hidden; */
}
.back {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-backface-visibility: unset;
  backface-visibility: unset;
}
